// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS
$color-black: #333;
$color-white: #fff;
$color-blue: #467bbe;

$body-background: #f7f7f7;

// Code colors
$color-clas: #1E78BA;
$color-var:  #BB8844;
$color-var2: #01a1Bf;
$color-dgt:  #DD3624;
$color-fun:  #929200;
$color-this: #d73a49;
$color-punct: #000;
///$color-tild: ;
$color-comment: #aaa;


// FONT
$font-family-roboto: "Roboto", sans-serif;


// MENU
$menu-translateX: 60vw;


// FOUNDATION

$global-width: 1200px;
$grid-column-count: 12;

$breakpoint-classes: (small medium large xlarge xxlarge);


$grid-margin-gutters: (small: 20px, medium: 25px);
