// stylelint-disable at-rule-empty-line-before
@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "checkbox-awesome";

html {
	background: url("../images/preloader.gif") center 40% no-repeat;
	min-height: 100%;
}

body {
	display: none;
	font-family: $font-family-roboto;
	color: $color-black;
	background: $body-background;
	padding-top: 0;
	padding-bottom: 10px;

	&.body--blured {

		.top-header, #container, .footer {
			filter: blur(1px);
		}
	}
}

@media screen and (min-width: 640px) and (max-width: 979px) {
	.grid-container > .grid-x {
		padding-left: 0!important;
		padding-right: 0!important;
	}
}

h1, h2, h3, h4, h5, h6 {
	font-family: $font-family-roboto;
	font-weight: normal;
	width: 100%;
}

@media print, screen and (min-width: 40em) {
	.h1, h1 {
		font-size: 2.2rem;
	}
	.h2, h2 {
		 font-size: 2rem;
	}
	.h3, h3 {
		font-size: 1.6rem;
	}
}

[type=color], [type=date], [type=datetime-local], [type=datetime], [type=email],
[type=month], [type=number], [type=password], [type=search], [type=tel],
[type=text], [type=time], [type=url], [type=week], textarea {
	border: none;
	border-bottom: 2px solid #333;
	box-shadow: none !important;
	background-color: transparent;
	font-size: 12.5px;
	letter-spacing: 1px;
	margin-bottom: 18px;
	font-family: $font-family-roboto;
	color: #666;
	font-weight: 400;
	padding-left: 0;
	padding-right: 0;
}

[type=color]:focus, [type=date]:focus, [type=datetime-local]:focus, [type=datetime]:focus, [type=email]:focus, [type=month]:focus, [type=number]:focus, [type=password]:focus, [type=search]:focus, [type=tel]:focus, [type=text]:focus, [type=time]:focus, [type=url]:focus, [type=week]:focus, textarea:focus {
	border: none;
	border-bottom: 2px solid $color-blue;
	color: $color-blue;
}

.nowrap {
	white-space: nowrap;
}

::selection {
	background: rgba(77,145,226,.21);
}
/*
	Content
*/
.top-header {
	margin-bottom: 12px;
	background-color: $color-blue;

	.grid-x {
		padding-top: 4px!important;
		padding-bottom: 2px!important;
	}

	h1, h2 {
		color: $color-white;
	}

	h1 { font-size: 1.5rem; margin-bottom: 0; position: relative; top: 3px; }

	&__buttons {
		padding: 5px;

		& > div {
			width: 25px;
			height: 25px;
			display: inline-block;
			vertical-align: middle;
			margin: 0 5px;
		}
	}

	@media screen and (min-width: 64em) {
		margin-bottom: 20px;

		h1 { font-size: 1.7rem; top: 1px; }
	}
}

.count-of-lng {
	display: inline-block;
	background-color: $color-white;
	color: $color-blue;
	border-radius: 50%;
	line-height: 25px;
	text-align: center;
	cursor: default	;
}

.settings-button {
	margin-left: 30px!important;
	cursor: pointer;

	svg {
		height: 25px;
		width: 24px;
	}
}

#container {

	.grid-container {

		& > div {
			margin-bottom: 0;
		}
	}
}

.grid-container {
	padding-right: 0;
	padding-left: 0;

	& > .grid-x {
		margin-bottom: 2px;
		padding: 1rem;
	}

	.checkbox, .radio {
		margin-bottom: 20px;
	}

	@media print, screen and (min-width: 40em) {

		padding-right: .78125rem;
		padding-left: .78125rem;
	}

}


.form-container {
	position: relative;

	form {
		margin-top: 10px;
	}
}


.top-block {
	@media screen and (max-width: 800px) {
		padding-top: 5px!important;
	}
	padding-bottom: 0!important;
	text-transform: uppercase;
	font-size: 90%;
}

.main-block {
	min-height: 300px;
	height: 78vh;

	.fn-container {
		font-family: Consolas, "Liberation Mono", Courier, monospace;
		background-color: #fff;
		border: 1px solid;
		border-top: none;
		border-color: transparent #f1f1f1 #e6e6e6;
		font-size: 85%;
		line-height: 1.8;
		position: relative;
		margin-bottom: 3px;
		border-radius: 1px;

		&[data-font-size='1'] {
			font-size: 85%;
		}
		&[data-font-size='2'] {
			font-size: 85%;
		}
		&[data-font-size='3'] {
			font-size: 85%;
		}
		&[data-font-size='4'], &[data-font-size='5'],  &[data-font-size='6'] {
			font-size: 84%;
		}

		.code-wrapper {
			color: #2808A9; // #0068b4;
			height: 100%;
			overflow: auto;
			padding: 15px 5px 10px 15px;

			& > div:first-child { margin-right: 150px; }

			code { background: transparent; border:none; font-family:inherit; padding: 0; }
			i { font-style: normal; }

			.clas{ color: $color-blue; background-color: #f6f6f6; border-bottom: 1px solid $color-blue; }
			.vars, .obj { color: $color-var; }
			.vars2{ color: $color-blue;}
			.dgt  { color: $color-this; }
			.fun  { color: $color-fun; }
			.comment { color: $color-comment;
				* { color: $color-comment!important; }
			}
			.this{ color: $color-this; font-weight: normal;}
			.punct{ color: $color-punct;}
			.tild{ color: $color-blue;}

			.space-line { height: 10px; }
		}

		.title-of-lng {
			font-size: 13px;
			color: black;
			position: absolute;
			top: 0;
			right: 0;
			padding: 5px 10px;
			background-color: #ddd;
			opacity: .4;
		}

	}

	.text-container {
		background-color: #f0f0f0;
		padding: 15px 8px 10px 15px;
		height: auto;
		overflow: auto;
		margin-bottom: 3px;
		border-radius: 1px;
		border: 1px solid;
		border-color: #f0f0f0 #e9e9e9 #ddd;

		h4 { font-size: 18px; margin-bottom: 15px;

			i { font-style: normal; font-family: Consolas,Liberation Mono,Courier,monospace; }

		}
		.description { font-size: 16px; margin-bottom: 10px; }
		.post-description { font-size: 13px; opacity: .85;
			code { margin-bottom: 6px; display: inline-block;}
		}

		small {
			color: #666; font-size: 80%;
		}
	}

	@media screen and (max-width: 800px) {
		.fn-container {
			width: 100%;
			height: 50%;
			margin-bottom: 3px;
		}
		.text-container {
			width: 100%;
			height: 49.5%;
		}
	}

	@media screen and (min-width: 801px) {
		.fn-container {
			margin-right: 2px;
			order: 1;
		}
		.text-container {
			margin-left: 2px;
			order: 2;
		}
	}

	@media print, screen and (min-width: 40em) {

		.fn-container {
			font-size: 90%;
			&[data-font-size='1'] {
				font-size: 90%;
			}
			&[data-font-size='2'] {
				font-size: 90%;
			}
			&[data-font-size='3'] {
				font-size: 90%;
			}
			&[data-font-size='4'], &[data-font-size='5'] , &[data-font-size='6'] {
				font-size: 85%;
			}

			.title-of-lng {
				font-size: 14px;
			}
		}
	}

	@media print, screen and (min-width: 64em) {
		//height: 300px;
	}

}


.bottom-block {
	padding-top: 0!important;
	padding-bottom: 0!important;
}

.control {
	text-align: center; position: relative;

	& > div {
		position: relative;
		left: 2px;
		border-radius: 50%;
		transition: 0.25s opacity, background-color 0.2s;
		opacity: .5;
		cursor: default;
		display: inline-block;
		vertical-align: middle;
		width: 50px;
		height: 50px;
		margin: 0 5px;
		background-color: rgba(70,123,190,0.1);

		&.is-disabled {
			color: #ccc!important;
			opacity: 0.25;
		}

		&:not(.is-disabled):hover {
			opacity: 1;
			cursor: pointer;

			svg {
				fill: $color-blue;
			}
		}

		&:not(.is-disabled):active {
			background-color: rgba(70,123,190,0.5);
		}

		svg {
			fill: #666;
			position: relative;
		}
	}

	&__start-btn {
		color: #3adb76;

		svg   { width: 58%; }
		.play { width: 78%; left: 7px;}
	}

	&__next-btn, &__prev-btn {
		width: 40px!important;
		height: 40px!important;

		svg {
			left: 3px; width: 58%;
		}
	}

	&__prev-btn svg {
		left: -3px;
	}

	&__mix-btn {

		svg {
			left:0;
			width:84%;
		}
	}
}

#mix-button {
	width: 35px;
	height: 35px;
	position: absolute;
	right: 0;
	left: auto;
	top: 6px;
}

.footer {
	position: fixed;
	left: 0;
	top: 100%;
	margin-top: -5px;
	width: 100%;
	height: auto;
	background-color: #eee;
	border-top: 1px solid #e5e5e5;
	transition: all .3s;

	&.active {
		top: auto; bottom: -1px;
	}

	&__handle {
		position: absolute;
		top: 5px;
		left: 50%;
		width: 90px;
		height: 25px;
		margin-left: -45px;
		background-color: transparent;
		text-align: center;
		opacity: .4;
		transition: opacity .2s;
		&:hover {
			opacity: 1;
			cursor: pointer;
		}

		svg {
			fill: rgba(0,0,0,0.5);
			width: 32px;
			height: 8px;
		}
	}
}


.settings {
	margin-bottom: 0;
}

svg {
	fill: $color-white;
	display: inline-block;
	vertical-align: middle;
	width: 100%;
	height: inherit;
}

.cbalink { display: none; }

.main-menu {
	transition: transform 0.25s;
	transform: translateX($menu-translateX);
	border-left: 1px solid rgba(0,0,0,0.1);
	position: fixed;
	top: 0;
	right: 0;
	width: 60vw;
	height: 100vh;
	background-color: white;
	overflow: auto;
	z-index: 999;

	&.is-active {
		transform: translateX(0);
		box-shadow: 0 0 3px rgba(0,0,0,0.2);
	}

	.languages {
		margin-bottom: 15px;
		margin-top: 20px;

		.cell {
			margin-bottom: 10px;

			.lang_sections {
				display: none;
			}

			&.is-active {

				.lang_sections {  }
			}

			.element {
				margin-bottom: 2px;
				transition: opacity 0.2s;
				color: #111;
				font-size: 90%;
				padding-left: 28px;
				cursor: pointer;
				opacity: 0.5;
				&:hover { opacity: .75; }

				&.is-active:not(.check-all) {
					background: url("../images/element-on.png") 7px center no-repeat;
					opacity: 0.8;

					&:hover { opacity: 1; }
				}

				&.check-all {
					color: #111; font-size: 80%;
					margin-top: 5px;
					margin-bottom: 10px;
					opacity: 0.75;
					&:hover {
						color: $color-blue;
						opacity: 0.9;
					}
				}
			}
		}
	}

	.checkbox {
		margin-bottom: 10px;
	}


	@media screen and (min-width: 480px) {
		transform: translateX(260px);
		width: 250px;
		min-width: 200px;
	}
	@media screen and (min-width: 40em) {
		transform: translateX(270px);
		width: 250px;
	}
	@media screen and (min-width: 64em) {
		transform: translateX(330px);
		width: 300px;
	}
}

.backdrop {
	display: none;
	position: fixed;
	top: 0; left: 0;
	width: 100%;
	height: 100%;
	background: #fff;
	z-index: 900;
	opacity: 0;
}
